import React from 'react';
import { Link } from 'react-router'

function Home() {
  return (
    <section className="container mt-5">
        <div className="row ">
            <div className="col-md-6 ">
            <img src="./imgs/aladdin.jpg" className="  w-100   homeImg " alt="aladdin photo" />
            </div>
            <div className="home home-content text-center  col-md-6 mt-lg-5">
            <h2 className=" mt-lg-5 pt-lg-5">welcome to </h2>
            <h3 className="text-success mb-lg-3 mt-lg-3 px-2"> Burger <span className="spanicon">&amp;</span> SWEETS </h3>
            <p className="fa-2x">Which serves a burger with municipal meat and fresh chicken..</p>
            <div className=" d-flex justify-content-center  pt-lg-5">
                <Link className="btn btn-outline-success home-btn"  to="/Menu">View Menu</Link>
            </div>
            </div>   
        </div>
    </section>

  )
}

export default Home