import  { useContext, useState } from 'react'
import { apiValue } from '../Data/AllData';
import './Allmenu.css';
import { useCart } from 'react-use-cart';

function Page() {
    const { addItem } = useCart();
    const products=useContext(apiValue)
    const [search,setsearch]=useState("")
    const getsearch = (event)=>{
        setsearch(event.target.value);
    }
  return (
    <div>
        <div className="container mt-5 pt-5">
             <input onChange={getsearch} type="search"  className='my-5 searchinput form-control ' placeholder="Search"/>
            <div className="row">
               
                {products.map((item)=>{
                   
                    return search==''?
                    (
                        <div className="col-md-4 mt-5 text-center">
                            <div className='d-flex justify-content-center align-items-center'>
                               
                                <div className="productContent">
                                     <h4>{item.name}</h4>
                                     <h5>{item.description}</h5>
                                     <h6>{item.price} £e</h6>
                                       <button className="shop-btn btn btn-success" onClick={() => addItem(item)}>Add to cart</button>
                                </div>
                                
                                <img src={item.image} alt="" className='imgMenu ' style={{height:"300px"}} />
                               
                            </div>
                            

                        </div>
                       

                    ):item.name.toLowerCase().indexOf(search.toLowerCase())==0?(
                         <div className="col-md-4 mt-5 text-center">
                            <div className='product-card d-flex justify-content-center align-items-center'>
                               
                                <div className="productContent">
                                     <h4>{item.name}</h4>
                                     <h5>{item.description}</h5>
                                     <h6>{item.price} £e</h6>
                                      <button className="Menu-btn btn btn-success" onClick={ ()=>addItem(item) }>Add to cart</button>
                                </div>
                                 <div className="imgBox">
                                    <img src={item.image} loading='lazy' alt="" className=' shopImg'  />
                                </div>
                            </div>
                            

                        </div>

                    ):null
                })}
            </div>
        </div>
    </div>
  )
}
function Menu(){
    return(
         <div>
            <Page/>
         </div>

    )
   
}

export default Menu;