
import  Navbar from '../Components/Navbar';
import './About.css';
import { Link } from 'react-router';
import { CartProvider } from 'react-use-cart';
import delta from '../Assets/17148720776636df0d4eded_IMG_7370.jpeg'

function AboutUs() {
  return (
    <CartProvider className='teeest'>
        <Navbar/>
           {/* about */}
        <div className="container-fluid aboutUs  ">
            <div className='row mt-5 pt-5 text-center'>
              <div className='col-md-10 mt-5  m-auto'>
                <img className='deltaImg ' src={delta} alt="" />

              </div>


            </div>
            <div className="row  mt-5 ">

                <div className="col-md-6 pt-2 mb-5 pb-2 text-center">
                <h2 className="mb-3 text-success fa-2x ">About Aladdin:</h2>
                <p>Aladdin offers beef burgers, chicken burgers, cookies, babas, milkshakes, coffee and ice cream. You can make your own burger and at our open kitchen. Our beef is from a reputable butcher who has his own farm, and our chicken is purchased fresh from our suppliers. Our products are made in house. You can ask crew members to let you enter our kitchen to make your burger yourself.
                </p>
                </div>
                <div className="col-md-3 col-sm-6  pt-2 mb-5 pb-2  text-center  ">
                <h3 className="mb-3">Page Transparency:</h3>
                <ul className="list-group list-group-flush ">
                    <li className="list-group-item"> <i className="fa-solid fa-calendar-days px-2 text-success"></i> Created : December 31,2014</li>
                    <li className="list-group-item"><i className="fa-solid fa-film px-2 text-success" />this page is not currently running ads.</li>
                    <li className="list-group-item"> <i className="fa-solid fa-user px-2 text-success" />people who manage this page</li>
                </ul>
                </div>
                <div className="col-md-3 col-sm-6   pt-2 mb-5 pb-2 text-center ">
                <h3 className="mb-3 " id="Contact">Contact Info:</h3>
                <ul className="list-group list-group-flush">
                    <li className="list-group-item"><i className=" fa-solid fa-phone px-2 text-success"> </i><Link to="tel: 01062785578" className="text-dark abouticon" >01284000023</Link></li>
                    <li className="list-group-item"><i className=" fa-brands fa-instagram px-2 text-success" /> <Link to="https://www.instagram.com/aladdin_burger/?hl=ar" className="text-dark abouticon"> instagram</Link></li>
                    <li className="list-group-item"><i className=" fa-brands fa-facebook px-2 text-success" /><Link to="https://web.facebook.com/AladdinBurger2020/?locale=ar_AR&_rdc=1&_rdr" className="text-dark abouticon"> Facebook</Link></li>
                </ul>
                </div>
            </div>
    
        </div>
</CartProvider>

  )
}

export default AboutUs