// export default Allcart
import { CartProvider, useCart } from "react-use-cart";
import Navbar from "../Components/Navbar";
import { Link } from "react-router-dom";
import './Cart.css';
import logo from '../Assets/aladdin.jpg';
import { useEffect, useState } from "react";
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Cart() {
  const {
    isEmpty,
    totalUniqueItems,
    items,
    updateItemQuantity,
    removeItem,
    cartTotal
  } = useCart();

  const [selectedOptions, setSelectedOptions] = useState(() => {
    const saved = localStorage.getItem("selectedOptions");
    return saved ? JSON.parse(saved) : {};
  });

  const handleCheckboxChange = (itemId, step, option) => {
    setSelectedOptions(prev => {
      const updated = { ...prev };
      if (!updated[itemId]) updated[itemId] = { step1: [], step2: [], step3: [] };
      const stepArray = updated[itemId][step];
      if (stepArray.includes(option)) {
        updated[itemId][step] = stepArray.filter(opt => opt !== option);
      } else {
        updated[itemId][step] = [...stepArray, option];
      }
      localStorage.setItem("selectedOptions", JSON.stringify(updated));
      return updated;
    });
  };

  const calculateExtra = (itemId) => {
    const selected = selectedOptions[itemId];
    if (!selected) return 0;
    let extra = 0;
    const freeLimits = { step1: 2, step2: 2, step3: 1 };
    const stepPrices = { step1: 5, step2: 5, step3: 12 };
    Object.keys(freeLimits).forEach(step => {
      const count = selected[step]?.length || 0;
      const over = count - freeLimits[step];
      if (over > 0) extra += over * stepPrices[step];
    });
    return extra;
  };

 const handleSubmitOrder = () => {
  const name = document.querySelector('input[placeholder="Name"]').value.trim();
  const phone = document.querySelector('input[placeholder="Phone Number"]').value.trim();
  const address = document.querySelector('input[placeholder="Address"]').value.trim();
  const notes = document.querySelector('textarea[placeholder="Notes"]').value.trim();

  if (!name || !phone || !address) {
    toast.error("❌ من فضلك املأ الاسم ورقم الهاتف والعنوان قبل إرسال الطلب.");
    return;
  }

  let message = `طلب جديد من ${name}\n`;
  message += `📞 رقم الهاتف: ${phone}\n📍 العنوان: ${address}\n`;
  if (notes) {
    message += `📝 ملاحظات: ${notes}\n`;
  }
  message += `\n`;

  items.forEach((item, index) => {
    message += `🛒 منتج ${index + 1}:\n`;
    message += `- الاسم: ${item.name}\n`;
    message += `- الكمية: ${item.quantity}\n`;
    message += `- السعر الأساسي: ${item.price} جنيه\n`;

    const extras = selectedOptions[item.id];
    if (extras) {
      Object.keys(extras).forEach((step) => {
        if (extras[step].length) {
          message += `- ${step}: ${extras[step].join(", ")}\n`;
        }
      });
    }

    const extraPrice = calculateExtra(item.id) * item.quantity;
    if (extraPrice > 0) {
      message += `- تكلفة الإضافات: ${extraPrice} جنيه\n`;
    }

    message += '\n';
  });

  const total = items.reduce((acc, item) => acc + (item.price + calculateExtra(item.id)) * item.quantity, 0);
  message += `💰 الإجمالي: ${total} جنيه`;

  toast.success("✅ تم إرسال الطلب بنجاح! سيتم تحويلك إلى الواتساب الآن...");
  setTimeout(() => {
    const whatsappLink = `https://wa.me/201062785578?text=${encodeURIComponent(message)}`;
    window.open(whatsappLink, "_blank");
  }, 3000); // تأخير علشان التوست يظهر الأول
};



  if (isEmpty) return (
    <div className="mt-5 pt-3 d-flex justify-content-center align-items-center">
      <h3 className='text-center emptyCart pt-5 mt-5'>Your cart is empty</h3>
      <h5 className="Continue ms-2 pt-5 mt-5">
        <Link to="/Menu" className="text-body Continue">
          Continue shopping <i className="fas fa-long-arrow-alt-right me-2 arrowIcon text-success" />
        </Link>
      </h5>
    </div>
  );

  return (
    <>
      <section className="h-100 mt-2 h-custom cartBody">
        <div className="container mt-5 py-5 h-100">
          <div className="row d-flex justify-content-center align-items-center h-100">
            <div className="col">
              <div className="card">
                <div className="card-body p-4">
                  <div className="row">
                    <div className="col-lg-7">
                      <h5 className="mb-3 Continue">
                        <Link to="/Menu" className="Continue">
                          <i className="fas fa-long-arrow-alt-left me-2 arrowIcon text-success" />Continue shopping
                        </Link>
                      </h5>
                      <hr />
                      <div className="d-flex justify-content-between align-items-center mb-4">
                        <div className="text-center m-auto">
                          <h2 className="mb-1 orange">Shopping cart</h2>
                          <p className="mb-0 orange">You have {totalUniqueItems} items in your cart</p>
                        </div>
                      </div>
                      {items.map((item) => {
                        const extra = calculateExtra(item.id);
                        return (
                          <div key={item.id} className="card mb-3">
                            <div className="card-body">
                              <div className="d-flex justify-content-between">
                                <div className="d-flex flex-row align-items-center">
                                  <div>
                                    <img src={item.image} className="img-fluid" alt="Shopping item" style={{ width: 100 }} />
                                  </div>
                                  <div className="ms-3">
                                    <h5 className='item-name'>{item.name}</h5>
                                    <p className="small mb-0 opacity-75">{item.description}</p>
                                  </div>
                                </div>
                                <div className="d-flex flex-row align-items-center">
                                  <div className='d-flex flex-row align-items-center' style={{ width: 100 }}>
                                    <button className='updatebtn btn btn-warning' onClick={() => updateItemQuantity(item.id, item.quantity - 1)}>-</button>
                                    <h5 className="fw-normal mb-0 mx-2">{item.quantity}</h5>
                                    <button className='me-5 updatebtn btn btn-success' onClick={() => updateItemQuantity(item.id, item.quantity + 1)}>+</button>
                                  </div>
                                  <div style={{ width: 80 }}>
                                    <h5 style={{ color: '#565acf' }} className="mb-0 ms-2">{(item.price + extra) * item.quantity}£e</h5>
                                  </div>
                                  <a onClick={() => removeItem(item.id)} href="#" style={{ color: '#f17732' }}><i className="fas fa-trash-alt" /></a>
                                </div>
                              </div>
                            </div>
                            <div className="text-center steps">
                              {[1, 2, 3].map(stepNum => {
                                const stepKey = `step${stepNum}`;
                                const stepOptions = stepNum === 1 ? ["خيار مخلل", "بصل مشوى", "خس", "طماطم", "فلفل هالبينو"]
                                  : stepNum === 2 ? ["شيدر", "تاى شيلى", "رانش", "الف جزيره", "باربيكيو", "مايونيز", "مايونيز حار", "تكساس"]
                                  : ["تركى مدخن", "اصابع موتزريلا", "اصابع شيدر هالبينو", "حلقات بصل"]
                                return (
                                  <div className="vegtableBox mt-4" key={stepKey}>
                                    <h3 className="khtwa">*الخطوه {stepNum === 1 ? "الاولى" : stepNum === 2 ? "الثانيه" : "الثالثه"}</h3>
                                    <h6 className="opacity-75">(Select {stepNum === 3 ? 1 : 2} items for free)</h6>
                                    <div>
                                      {stepOptions.map(opt => (
                                        <label key={opt} className="mx-2">
                                          {opt}
                                          <input
                                            type="checkbox"
                                            className="mx-2"
                                            checked={selectedOptions[item.id]?.[stepKey]?.includes(opt) || false}
                                            onChange={() => handleCheckboxChange(item.id, stepKey, opt)}
                                          />
                                        </label>
                                      ))}
                                    </div>
                                  </div>
                                )
                              })}
                            </div>
                          </div>
                        );
                      })}
                    </div>
                    <div className="col-lg-5">
                      <div className="card bg-success text-white rounded-3">
                        <div className="card-body">
                          <div className="d-flex justify-content-between align-items-center mb-4">
                            <img src={logo} className="img-fluid rounded-3 m-auto" style={{ width: 200, height: '100px' }} alt="Avatar" />
                          </div>
                          <form className="mt-4">
                            <input type="text" className="form-control form-control-lg mb-3" placeholder="Name" />
                            <input type="text" className="form-control form-control-lg mb-3" placeholder="Phone Number" />
                            <input type="text" className="form-control form-control-lg mb-3" placeholder="Address" />
                            <textarea className="form-control mb-3" placeholder="Notes"></textarea>
                          </form>
                          <hr className="my-4" />
                          <div className="d-flex justify-content-between mb-4">
                            <p className="mb-2">Total (Incl. extras)</p>
                            <p className="mb-2">
                              {items.reduce((acc, item) => acc + (item.price + calculateExtra(item.id)) * item.quantity, 0)} £e
                            </p>
                          </div>
                          <div className="text-center">
                            <button type="button" onClick={handleSubmitOrder} className="btn btn-submit btn-lg">
                              Submit
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <ToastContainer position="top-center" style={{padding:"10px"}} autoClose={4000} />
    </>
  );
}

function Allcart() {
  return (
    <CartProvider>
      <Navbar />
      <Cart />
    </CartProvider>
  );
}

export default Allcart;



